import React, { useEffect } from 'react';
import { useState } from 'react';
import { useDispatch } from 'react-redux';
import { Link } from 'react-router-dom';

import { signUp } from '../store/actions/auth';
import { history } from '../app';
import * as displayActions from '../store/actions/display';

const RegistrationPage = () => {
  const [firstName, setFirstName] = useState('');
  const [lastName, setLastName] = useState('');
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [password2, setPassword2] = useState('');
  const [phone, setPhone] = useState('');
  const [error, setError] = useState();

  const dispatch = useDispatch();
  const date = new Date();

  useEffect(() => {
    dispatch(displayActions.setPageSidebar(false));
    dispatch(displayActions.setPageHeader(false));
    dispatch(displayActions.setPageFooter(false));
    dispatch(displayActions.setPageContentFullWidth(true));
    return () => {
      dispatch(displayActions.setPageSidebar(true));
      dispatch(displayActions.setPageHeader(true));
      dispatch(displayActions.setPageFooter(true));
      dispatch(displayActions.setPageContentFullWidth(false));
    };
  }, []);

  useEffect(() => {
    if (error) {
      console.log(error);
    }
  }, [error]);

  const submitHandler = async (e) => {
    e.preventDefault();

    try {
      await dispatch(
        signUp(email, phone, `${firstName} ${lastName}`, password, password2)
      );
      history.push('/');
    } catch (err) {
      setError(err.message);
      // setIsLoading(false);
    }
  };

  return (
    <div className='register register-with-news-feed'>
      <div className='news-feed'>
        <div
          className='news-image'
          style={{
            backgroundImage: 'url(/assets/img/login-bg/login-bg-9.jpg)',
          }}
        ></div>
        <div className='news-caption'>
          <h4 className='caption-title'>
            <b>Market</b> Profit App
          </h4>
          <p></p>
        </div>
      </div>
      <div className='right-content'>
        <h1 className='register-header'>
          Sign Up
          <Link to='/'>
            <span className='float-right'>
              <button className='btn btn-primary'>
                <i className='fas fa-home fa-fw'></i>
              </button>
            </span>
          </Link>
          <small>
            Create your MarketProfit Account. It’s free and always will be.
          </small>
        </h1>
        <div className='register-content'>
          <form className='margin-bottom-0' onSubmit={submitHandler}>
            <label className='control-label'>
              Name <span className='text-danger'>*</span>
            </label>
            <div className='row row-space-10'>
              <div className='col-md-6 m-b-15'>
                <input
                  type='text'
                  className='form-control'
                  placeholder='First name'
                  value={firstName}
                  onChange={(e) => {
                    setFirstName(e.target.value);
                  }}
                  required
                />
              </div>
              <div className='col-md-6 m-b-15'>
                <input
                  type='text'
                  className='form-control'
                  placeholder='Last name'
                  value={lastName}
                  onChange={(e) => {
                    setLastName(e.target.value);
                  }}
                  required
                />
              </div>
            </div>
            <label className='control-label'>
              Email <span className='text-danger'>*</span>
            </label>
            <div className='row m-b-15'>
              <div className='col-md-12'>
                <input
                  type='text'
                  className='form-control'
                  placeholder='Email address'
                  value={email}
                  onChange={(e) => {
                    setEmail(e.target.value);
                  }}
                  required
                />
              </div>
            </div>
            <label className='control-label'>
              Phone <span className='text-danger'>*</span>
            </label>
            <div className='row m-b-15'>
              <div className='col-md-12'>
                <input
                  type='text'
                  className='form-control'
                  placeholder='Phone'
                  value={phone}
                  onChange={(e) => {
                    setPhone(e.target.value);
                  }}
                  required
                />
              </div>
            </div>
            <label className='control-label'>
              Password <span className='text-danger'>*</span>
            </label>
            <div className='row m-b-15'>
              <div className='col-md-12'>
                <input
                  type='text'
                  className='form-control'
                  placeholder='Password'
                  value={password}
                  onChange={(e) => {
                    setPassword(e.target.value);
                  }}
                  required
                />
              </div>
            </div>
            <label className='control-label'>
              Re-enter Password <span className='text-danger'>*</span>
            </label>
            <div className='row m-b-15'>
              <div className='col-md-12'>
                <input
                  type='password'
                  className='form-control'
                  placeholder='Re-enter Password'
                  value={password2}
                  onChange={(e) => {
                    setPassword2(e.target.value);
                  }}
                  required
                />
              </div>
            </div>
            <div className='checkbox checkbox-css m-b-30'>
              <div className='checkbox checkbox-css m-b-30'>
                <input type='checkbox' id='agreement_checkbox' value='' />
                <label htmlFor='agreement_checkbox'>
                  By clicking Sign Up, you agree to our
                  <Link to='/user/register-v3'>Terms</Link> and that you have
                  read our <Link to='/user/register-v3'>Data Policy</Link>,
                  including our <Link to='/user/register-v3'>Cookie Use</Link>.
                </label>
              </div>
            </div>
            <div className='register-buttons'>
              <button
                type='submit'
                className='btn btn-primary btn-block btn-lg'
                onSubmit={submitHandler}
              >
                Sign Up
              </button>
            </div>
            <div className='m-t-20 m-b-40 p-b-40 text-inverse'>
              Already a member? Click <Link to='/login'>here</Link> to login.
            </div>
            <hr />
            <p className='text-center'>
              &copy; MarketProfit All Right Reserved {date.getFullYear()}
            </p>
          </form>
        </div>
      </div>
    </div>
  );
};

export default RegistrationPage;
